import React, { Component } from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import BarChartIcon from '@material-ui/icons/ShowChart';
import DashboardIcon from '@material-ui/icons/ViewComfy';
import MovieFilterIcon from '@material-ui/icons/Movie';
import LocalActivityIcon from '@material-ui/icons/LocalActivity';
import SearchIcon from '@material-ui/icons/Search';

export class Icons extends Component {
  state = {
    buttons: [
      {
        primary: "Statystyki",
        icon: <BarChartIcon />,
        active: true
      },
      {
        primary: "Kategorie Filmów",
        icon: <DashboardIcon />,
        active: false
      },
      {
        primary: "Kanały",
        icon: <LocalActivityIcon />,
        active: false
      },
      {
        primary: "Filmy",
        icon: <MovieFilterIcon />,
        active: false
      },
      {
        primary: "Wyszukaj",
        icon: <SearchIcon />,
        active: false
      }
    ]
  };

  checkStatus = (status) => {
    if(status) {
      return {
        backgroundColor: 'steelblue'
      };
    }
  };

  changeDashboard = (dashboardId) => {
    if (dashboardId !== 4) {
      window.viz.getWorkbook().activateSheetAsync(dashboardId);
    }
    this.props.changeParentState(dashboardId);
  };

  handleClick = (buttonId) => (evt) => {
    if (this.props.isLoaded) {
      const newState = this.state.buttons.map((button, id) => {
        const { primary, icon } = button;
        return {
          primary,
          icon,
          active: buttonId === id
        };
      });
      this.changeDashboard(buttonId);
      this.setState({ buttons: newState });
    }
  };

  render () {
    return (<div>
        {
          this.state.buttons.map((button,id) => {
            const { primary, icon, active } = button;
            return (
              <ListItem key={ id } button style={ this.checkStatus(active) } onClick={ this.handleClick(id) } >
                <ListItemIcon>
                  { icon }
                </ListItemIcon>
                <ListItemText primary={ primary } />
              </ListItem>
            );
          })
        }
      </div>);
  }
}


