import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ListSubheader from '@material-ui/core/ListSubheader';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import VisibilityIcon from '@material-ui/icons/Visibility';
import PersonIcon from '@material-ui/icons/Person';
import MovieIcon from '@material-ui/icons/Movie';
import Divider from '@material-ui/core/Divider';

const styles = theme => ({
  root: theme.mixins.gutters({
    paddingTop: 16,
    paddingBottom: 16,
    marginTop: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 2,
    display: 'flex',
    justifyContent: 'center',
  }),
  actions: {
    display: 'flex',
    justifyContent: 'center',
  },
  card: {
    width: 345,
    boxShadow: 'none'
  },
  cardStat: {
    width: 345,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    boxShadow: 'none'
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
});

function SearchResults(props) {
  const { classes, channel } = props;

  return (
    <div style={ channel.snippet ? {display: 'block'} : {display: 'none'} }>
      <Paper className={classes.root} elevation={4}>
          <Card className={classes.card}>
            <CardMedia
              className={classes.media}
              image={ channel.snippet ? channel.snippet.thumbnails.medium.url : '' }
              title="User Photo"
            />
            <CardContent>
              <Typography gutterBottom variant="headline" component="h2">
                { channel.snippet ? channel.snippet.title : null }
              </Typography>
              <Typography>
                { channel.snippet ? channel.snippet.description : null }
              </Typography>
            </CardContent>
            <CardActions className = { classes.actions }>
              <Button target="_blank" href={channel.snippet ? 'https://www.youtube.com/user/'+channel.snippet.customUrl : null } size="small" color="primary">
                Odwiedź na YouTube
              </Button>
            </CardActions>
          </Card>
        <Card className={classes.cardStat}>
          <List subheader={<ListSubheader component="div">Statystyki</ListSubheader>}>
            <Divider/>
            <ListItem divider>
              <Avatar>
                <VisibilityIcon />
              </Avatar>
              <ListItemText primary={ channel.snippet ? channel.statistics.viewCount : null } secondary="Wyświetleń" />
            </ListItem>
            <ListItem divider>
              <Avatar>
                <PersonIcon />
              </Avatar>
              <ListItemText primary={ channel.snippet ? channel.statistics.subscriberCount : null } secondary="Subskrypcji" />
            </ListItem>
            <ListItem>
              <Avatar>
                <MovieIcon />
              </Avatar>
              <ListItemText primary={ channel.snippet ? channel.statistics.videoCount : null } secondary="Filmów" />
            </ListItem>
          </List>
        </Card>
      </Paper>
    </div>
  );
}

export default withStyles(styles)(SearchResults);


