import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Downshift from 'downshift';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import MenuItem from '@material-ui/core/MenuItem';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import SearchResults from './SearchResults';
import Loader from './Loader';

const theme = createMuiTheme({
  palette: {
    type: 'light'
  },
});

let suggestions = [
  { label: 'Majes17' },
];

function renderInput(inputProps) {
  const { InputProps, classes, ref, ...other } = inputProps;

  return (
    <TextField
      InputProps={{
        inputRef: ref,
        classes: {
          root: classes.inputRoot,
        },
        ...InputProps,
      }}
      {...other}
    />
  );
}

function renderSuggestion({ suggestion, index, itemProps, highlightedIndex, selectedItem }) {
  const isHighlighted = highlightedIndex === index;
  const isSelected = (selectedItem || '').indexOf(suggestion.label) > -1;

  return (
    <MenuItem
      {...itemProps}
      key={suggestion.label}
      selected={isHighlighted}
      component="div"
      style={{
        fontWeight: isSelected ? 500 : 400
      }}
    >
      {suggestion.label}
    </MenuItem>
  );
}
renderSuggestion.propTypes = {
  highlightedIndex: PropTypes.number,
  index: PropTypes.number,
  itemProps: PropTypes.object,
  selectedItem: PropTypes.string,
  suggestion: PropTypes.shape({ label: PropTypes.string }).isRequired,
};

function getSuggestions(inputValue) {
  let count = 0;

  return suggestions.filter(suggestion => {
    const keep =
      (!inputValue || suggestion.label.toLowerCase().indexOf(inputValue.toLowerCase()) !== -1) &&
      count < 5;

    if (keep) {
      count += 1;
    }

    return keep;
  });
}

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  container: {
    flexGrow: 1,
    position: 'relative',
  },
  paper: {
    position: 'absolute',
    zIndex: 1,
    marginTop: theme.spacing.unit,
    left: 0,
    right: 0,
  },
  chip: {
    margin: `${theme.spacing.unit / 2}px ${theme.spacing.unit / 4}px`,
  },
  inputRoot: {
    flexWrap: 'wrap',
  },
});

class IntegrationDownshift extends Component {
  state = {
    fetchingData: false,
    currentChannel: {}
  };

  handleChange = (evt) => {
    this.setState({fetchingData: true});
    return fetch(`https://data-champion-2018.herokuapp.com/users/${evt}`)
      .then(response => response.json())
      .then(data => {
        if (data.items.length > 0) {
          this.setState({
            currentChannel: data.items[ 0 ],
            fetchingData: false,
          });
        } else {
          this.setState({
            fetchingData: false,
          });
        }
      })
      .catch(err => console.log(err));
  };

  componentDidMount() {
    fetch('https://data-champion-2018.herokuapp.com/movies')
      .then(response => response.json())
      .then(data => data.map(movie => movie["Nazwa kanalu"]))
      .then(allChannels => [...new Set(allChannels)].sort())
      .then(channels => channels.map(channel => {
        return { label: channel }
      }))
      .then(channelsWithLabels => {
        suggestions = channelsWithLabels;
      })
      .catch(err => console.log(err));
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={ classes.root } style={this.props.currentView === 4 ? {display: 'block'} : {display: 'none'}}>
        <MuiThemeProvider theme={ theme }>
          <Downshift onChange={ this.handleChange }>
            { ({ getInputProps, getItemProps, isOpen, inputValue, selectedItem, highlightedIndex }) => (
              <div className={ classes.container }>
                { renderInput({
                  fullWidth: true,
                  classes,
                  InputProps: getInputProps({
                    placeholder: 'Znajdź YouTubera!',
                    id: 'integration-downshift-simple',
                  }),
                }) }
                { isOpen ? (
                  <Paper className={ classes.paper } square>
                    { getSuggestions(inputValue).map((suggestion, index) =>
                      renderSuggestion({
                        suggestion,
                        index,
                        itemProps: getItemProps({ item: suggestion.label }),
                        highlightedIndex,
                        selectedItem,
                      }),
                    ) }
                  </Paper>
                ) : null }
              </div>
            ) }
          </Downshift>
          { this.state.fetchingData ? <Loader /> : <SearchResults channel={ this.state.currentChannel}/> }
        </MuiThemeProvider>
      </div>
    );
  }
}


IntegrationDownshift.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(IntegrationDownshift);



