import React, { Component } from 'react';
import tableau from 'tableau-api';

class Dashboard extends Component {
  state = {
    container: "vizContainer",
    url: "https://public.tableau.com/views/4miesicepolskiegoYouTube/Statystyki",
    name: 'dash',
  };

  initializeDashboard() {
    const containerDiv = document.getElementById(this.state.container);
    const options = {
      width: '1050px',
      height: '1450px',
      hideToolbar: true,
      hideTabs: true,
      onFirstInteractive: () => {
        this.props.loadedDash(true);
      }
    };

    const viz = new window.tableau.Viz(containerDiv, this.state.url, options);
    console.log(tableau);
    window.viz = viz;
  }

  componentDidMount() {
    this.initializeDashboard();
  }

  render () {
    return (
      <div id="vizContainer" style={ this.props.currentView < 4 ? {display: 'flex'} : {display: 'none'} }></div>
    );
  }
}

export default Dashboard;



